/**
 * WordPress dependencies
 */
import { __ } from '@wordpress/i18n';
import { Icon, blockDefault } from '@wordpress/icons';

function InserterNoResults( { icon, label } ) {
	return (
		<div className="patternswp-inserter__no-results">
			<div>
				<Icon
					className="patternswp-inserter__no-results-icon"
					icon={ icon ? icon : blockDefault }
				/>
				<p>
					{ label
						? label
						: __( 'No results found.', 'patternswp' ) }
				</p>
			</div>
		</div>
	);
}

export default InserterNoResults;
