/**
 * External dependencies.
 */
import { isEmpty } from 'lodash';

/**
 * WordPress dependencies.
 */
import { __ } from '@wordpress/i18n';
import { render, useState, useMemo, useCallback, useEffect } from '@wordpress/element';
import { dispatch, subscribe } from '@wordpress/data';
import { Button, Modal } from '@wordpress/components';

/**
 * Internal dependencies
 */
import PatternExplorer from './pattern-explorer';
import usePatternsState from './core-hooks/use-patterns-state';

/**
 * Render the header toolbar button and the accompanying pattern explorer modal.
 *
 * @since 0.1.0
 * @return {string} Return the rendered JSX for the Pattern Explorer Button
 */
function HeaderToolbarButton() {
    const [isModalOpen, setIsModalOpen] = useState(false);
    const [allPatterns, allCategories, allCategoryTypes] = usePatternsState();
    const fetchedCategoryTypes = allCategoryTypes === 'fetching' ? [] : allCategoryTypes;

    // Check if a pattern category has an assigned pattern category type.
    const hasRegisteredCategoryType = useCallback(
        (category) => {
            if (!category.categoryTypes || !category.categoryTypes.length) {
                return false;
            }

            return category.categoryTypes.some((type) =>
                fetchedCategoryTypes.some(
                    (categoryType) => categoryType.name === type
                )
            );
        },
        [fetchedCategoryTypes]
    );

    const onCloseModal = () => { 
        setIsModalOpen(false);
    };

    // Remove any categories without patterns and not in selected categories.
    const populatedCategories = useMemo(() => {
        const filteredCategories = allCategories.filter(category => category.name.startsWith('patternswp-'));
    
        // Create labels dynamically for categories
        const categorizedLabels = filteredCategories.map(category => ({
            ...category,
            label: category.name
                .replace('patternswp-', '')
                .replace(/-/g, ' ')
                .replace(/\b\w/g, char => char.toUpperCase())
        }));
    
        // Add the "All" option
        const allOption = {
            name: 'all',
            label: 'All',
        };
    
        return [allOption, ...categorizedLabels];
    }, [allCategories]);

    // Remove any pattern category type without populated pattern categories.
    const populatedCategoryTypes = useMemo(() => {
        const categoryTypes = fetchedCategoryTypes.filter((type) =>
            populatedCategories.some((category) =>
                category.categoryTypes?.includes(type.name)
            )
        );

        // If there are categories without types, create the Uncategorized type.
        if (
            populatedCategories.some(
                (category) => !hasRegisteredCategoryType(category)
            ) &&
            !categoryTypes.find((type) => type.name === 'uncategorized')
        ) {
            categoryTypes.unshift({
                name: 'uncategorized',
                label: __('Uncategorized', 'patternswp'),
                hideLabelFromVision: true,
            });
        }

        return categoryTypes;
    }, [populatedCategories, fetchedCategoryTypes]);

    // Could expand on this in the future, i.e. allow for a configurable
    // initial category. For now the initial category is the first category in
    // the first category type.
    const initialCategory = populatedCategories[0];

    // If there are no patterns, do not display the pattern explorer button.
    if (isEmpty(allPatterns)) {
        return null;
    }

    useEffect(() => {
        const observerCallback = (mutationsList) => {
            for (const mutation of mutationsList) {
                if (mutation.type === 'childList' && mutation.addedNodes.length) {
                    document.querySelectorAll('.components-dropdown.block-editor-inserter').forEach((dropdown) => {
                        addButton(dropdown);
                    });
                }
            }
        };

        const observer = new MutationObserver(observerCallback);
        observer.observe(document.body, { subtree: true, childList: true });

        const addButton = ($dropdown) => {
            if (!$dropdown.querySelector('.patternswp-appender-button')) {
                const $appenderButton = document.createElement('button');
                $appenderButton.type = 'button';
                $appenderButton.setAttribute('aria-haspopup', 'true');
                $appenderButton.className = 'components-button block-editor-inserter__toggle has-icon patternswp-appender-button';
                $appenderButton.innerHTML = `<svg width="20" height="20" viewBox="0 0 25 37" fill="#ffffff" xmlns="http://www.w3.org/2000/svg">
                    <mask id="mask0_671_1966" style="mask-type:luminance" maskUnits="userSpaceOnUse" x="0" y="18" width="9" height="19" fill="#ffffff" ><path d="M0 18.3303H8.23816V36.3607H0V18.3303Z" fill="#ffffff"/></mask>
                    <g mask="url(#mask0_671_1966)"><path fill-rule="evenodd" clip-rule="evenodd" d="M0.625738 32.0853L8.04102 36.3602V18.5413L-0.000976562 23.1771V31.7244L0.625738 32.0853Z" fill="#ffffff"/></g>
                    <mask id="mask1_671_1966" style="mask-type:luminance" maskUnits="userSpaceOnUse" x="10" y="9" width="15" height="18"><path d="M10.1934 9.198H24.1297V26.5898H10.1934V9.198Z" fill="#ffffff"/></mask>
                    <g mask="url(#mask1_671_1966)"><path fill-rule="evenodd" clip-rule="evenodd" d="M10.2178 17.2857V26.5548L16.0824 23.1758L24.1278 18.54V9.26831L16.0824 13.9041L10.2178 17.2857Z" fill="#ffffff"/></g>
                    <mask id="mask2_671_1966" style="mask-type:luminance" maskUnits="userSpaceOnUse" x="16" y="4" width="9" height="10"><path d="M16.0645 4.63403H24.1311V13.9821H16.0645V4.63403Z" fill="#ffffff"/></mask>
                    <g mask="url(#mask2_671_1966)"><path fill-rule="evenodd" clip-rule="evenodd" d="M16.083 13.9058L24.1284 9.26999L16.083 4.63672V13.9058Z" fill="#ffffff"/></g>
                    <mask id="mask3_671_1966" style="mask-type:luminance" maskUnits="userSpaceOnUse" x="0" y="0" width="17" height="14">
                    <path d="M0 0H16.2819V13.9813H0V0Z" fill="#ffffff"/></mask><g mask="url(#mask3_671_1966)"><path fill-rule="evenodd" clip-rule="evenodd" d="M8.04102 9.2691L16.083 4.63582L8.04102 0L-0.000976562 4.63582V13.9049L8.04102 9.2691Z" fill="#ffffff"/></g>
                </svg>`;
                $appenderButton.style.marginLeft = '8px';
                $dropdown.appendChild($appenderButton);
                $dropdown.style.display = 'flex';
                $dropdown.style.gap = '5px';

                $appenderButton.addEventListener('click', () => {
                    setIsModalOpen(true);
                });
            }
        };

        const checkButtonPresence = () => {
            const dropdowns = document.querySelectorAll('iframe[name="editor-canvas"]').forEach((iframe) => {
                const $dropdown = iframe.contentDocument.querySelector('.components-dropdown.block-editor-inserter');
                if ($dropdown) {
                    addButton($dropdown);
                }
            });
            setTimeout( checkButtonPresence, 200 );
        };

        checkButtonPresence();

        return () => {
            observer.disconnect();
        };
    }, []);

    return (
        <>
            <Button
                className="components-button is-button is-primary patternswp-button" // Add custom classes for styling
                onClick={() => setIsModalOpen(true)}
            >
                PatternsWP Library
            </Button>
            {isModalOpen && (
                <Modal
                    title={__('PatternsWP', 'patternswp')}
                    closeLabel={__('Close', 'patternswp')}
                    onRequestClose={onCloseModal}
                    className="patternswp_modal"
                    isFullScreen
                >
                <PatternExplorer
                    allPatterns={allPatterns}
                    initialCategory={initialCategory}
                    patternCategories={populatedCategories}
                    patternCategoryTypes={populatedCategoryTypes}
                    categoryTypes
                    onRequestClose={onCloseModal}
                />
                </Modal>
            )}
        </>
    );
}

/**
 * Add the header toolbar button to the block editor.
 */
subscribe(() => {
    const inserter = document.querySelector('#patternswp');

    // If the inserter already exists, bail.
    if (inserter) {
        return;
    }

    wp.domReady(() => {
        const toolbar = document.querySelector(
            '.edit-post-header-toolbar'
        );

        // If no toolbar can be found at all, bail.
        if (!toolbar) {
            return;
        }

        const buttonContainer = document.createElement('div');
        buttonContainer.id = 'patternswp';

        toolbar.appendChild(buttonContainer);

        render(
            <HeaderToolbarButton />,
            document.getElementById('patternswp')
        );
    });
});

/**
 * (Experimental) Add support for the pattern category type setting.
 *
 * @param {Array} settings All editor settings
 * @since 0.2.0
 */

/**
 * Add our custom entities for retrieving external data in the Block Editor.
 *
 * @since 0.2.0
 */
dispatch('core').addEntities([
    {
        label: __('Pattern Category Types', 'patternswp'),
        kind: 'patternswp/v1',
        name: 'patternCategoryTypes',
        baseURL: '/patternswp/v1/pattern-category-types',
    },
]);
